@import "../../styles/colors";

.timeline-button {
  border: none;
  background-color: white;
  color: $gray;
  font-weight: 700;
  cursor: pointer;
  transition: 0.35s;
}

.timeline-button:hover {
  color: $blue;
}

.active-timeline {
  color: $blue;
}

.coin-button-container {
  padding: 15px 25px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
}

.chart-header-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: absolute;
  width: 100%;
}

.new-coin-input {
  width: 60px !important;
  font-weight: 700;
  border: none;
  outline: none;
  padding: 0px 1px;
  border-bottom: 1px solid $blue;
}

.new-coin-input::placeholder {
  color: $gray;
}

.new-coin-input:focus {
  color: $blue;
}

.coin-list-item,
.browse-all {
  margin: 0;
  padding: 0 5px;
  transition: 0.25s;
  cursor: pointer;
  border: none;
  width: 100%;
  text-align: left;
  background-color: transparent;
}

.browse-all {
  white-space: nowrap;
  background-color: $blue;
  color: white;
  padding: 5px;
  margin-top: 5px;
  border-radius: 5px;
}

.coin-list-item:hover {
  background-color: rgba(0, 82, 255, 0.15);
}

.coin-search-container {
  width: 150px;
  font-size: 14px;
  position: absolute;
  background-color: rgba(185, 185, 185, 0.3);
}

.remove-button {
  color: $red !important;
}

.positive {
  color: rgb(19, 163, 19);
}

.negative {
  color: $red;
}

.percentage {
  padding-top: 5px;
  font-weight: 400;
  font-size: 15px;
  padding-left: 10px;
}

.input-container {
  margin-top: -2px;
  margin-left: 4px;
}

.coin-chart-header {
  color: $blue;
  font-weight: 700;
  font-size: 25px;
}

.line-container {
  background-color: white;
  width: 100%;
  height: 100%;
  border: 1px solid $border;
  margin-left: -1px;
  border-top: none;
  padding-top: 5vh;
}

.chart-wrapper-container {
  width: 70vw;
  height: 70vh;
  background-color: white;
  border: 1px solid $border;
  position: relative;
}
